    width: 350px;
  }
}
/* font style about */
.gridContainer {
  margin: "auto";
  text-align: "justify";
}

/* sectinon team */
.perTeam {
  margin: 50px auto;
  text-align: center;
  line-height: 0.2;
}
.perTeam .imgBox{
  filter: scale(0%);
  border-radius: 50%;
  height: 45%;
  width: 45%;
  margin: auto;
  padding: 0;
  box-shadow: 0 5px 11px 0 rgba(0, 0, 0, 0.18), 0 4px 15px 0 rgba(0, 0, 0, 0.15);
  /* filter: saturate(0%); */
}
.perTeam img{
  height: 100%;
  width: 100%;
  border-radius: 50%;
} 
.imgBox{
	position: relative;
	display: inline-block;
	overflow: hidden;
	max-width: 100%;
	height: auto;
}

.imgBox .hvrbox-layer_bottom {
	display: block;
}
.imgBox .hvrbox-layer_top {
	opacity: 0;
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 100%;
	height: 100%;
	background: rgba(0, 0, 0, 0.6);
	color: #fff;
	padding: 15px;
	-moz-transition: all 0.3s ease-in-out 0s;
	-webkit-transition: all 0.3s ease-in-out 0s;
	-ms-transition: all 0.3s ease-in-out 0s;
	transition: all 0.3s ease-in-out 0s;
}
.imgBox:hover .hvrbox-layer_top,
.imgBox.active .hvrbox-layer_top {
	opacity: 1;
}
.imgBox .hvrbox-text {
	text-align: center;
	font-size: 18px;
	display: inline-block;
	position: absolute;
	top: 50%;
	left: 50%;
	-moz-transform: translate(-50%, -50%);
	-webkit-transform: translate(-50%, -50%);
	-ms-transform: translate(-50%, -50%);
	transform: translate(-50%, -50%);
}
.imgBox .hvrbox-text_mobile {
	font-size: 15px;
	border-top: 1px solid rgb(179, 179, 179); /* for old browsers */
	border-top: 1px solid rgba(179, 179, 179, 0.7);
	margin-top: 5px;
	padding-top: 2px;
	display: none;
}
.imgBox.active .hvrbox-text_mobile {
	display: block;
}
.imgBox .hvrbox-layer_slideup {
	-moz-transform: translateY(100%);
	-webkit-transform: translateY(100%);
	-ms-transform: translateY(100%);
	transform: translateY(100%);
}
.imgBox:hover .hvrbox-layer_slideup,
.imgBox.active .hvrbox-layer_slideup {
	-moz-transform: translateY(0);
	-webkit-transform: translateY(0);
	-ms-transform: translateY(0);
	transform: translateY(0);
  
  cursor: pointer;
}
.perTeam h5 {
  font-family: "Open Sans";
  padding-top: 1%;
  font-size: 19px;
}
strong {
  font-weight: bolder;
}
.perTeam img:hover {
  cursor: pointer;
}

